
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AngularFirestore } from '@angular/fire/firestore';
import { Subscription } from 'src/app/shared/models/subscription.model';


@Injectable({
  providedIn: 'root'
})

export class SubscriptionService {
  public apiurl: string;
  subscriptions: Observable<any[]>;
  subcriptionsCollection : Array<Subscription>;

  constructor(
    public _http: HttpClient,
    private firestore: AngularFirestore
  ) { 
    this.subscriptions = firestore.collection('subscriptions').valueChanges();
  }

  getArticles(): Observable<any> { 
    return this.subscriptions;
  }

  createArticle(subscription): Observable<any> {
    // Add a new document in collection "subcription"
    this.firestore.collection("subscriptions").doc().set({
      name:subscription.name,
      lastname:subscription.lastname,
      city:subscription.city,
      country:subscription.country,
      whatsapp:subscription.whatsapp,
      email:subscription.email,
      time:subscription.time
    })
    .then(() => {
      console.log("Document successfully written!");
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
    });

    this.subscriptions = this.firestore.collection('subscriptions').valueChanges();
    return this.subscriptions;;
  }

  updateArticle(article): Observable<any> {
   

    const body = {
      'id': article.id,
      'price': article.price,
      'description': article.description,
      'jelpi_amount': article.jelpi_amount,
      'company_amount': article.company_amount,
    };

    return this._http.post(this.apiurl + 'articles/update/' + article.id, body, );
  }

  deleteArticle(subscription): Observable<any> {
    this.firestore.collection("cities").doc("DC").delete().then(() => {
        console.log("Document successfully deleted!");
    }).catch((error) => {
        console.error("Error removing document: ", error);
    });

    return this._http.delete(this.apiurl + 'articles/' );
  }

 
}
