import { SubscriptionService } from '../../services/subscription.service';
import { HttpClient } from '@angular/common/http'
import { Component, OnInit, ViewChild, AfterViewInit, ChangeDetectorRef } from '@angular/core'
import { MatPaginator } from '@angular/material/paginator'
import { MatSort } from '@angular/material/sort'
import { MatTableDataSource } from '@angular/material/table'
import { Router, ActivatedRoute } from '@angular/router'
import { FormGroup, FormControl, Validators } from '@angular/forms'
import {MatDialog} from '@angular/material/dialog';
import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material/snack-bar'
import { EditdialogComponent } from '../editdialog/editdialog.component'
import { DeleteDialogComponent } from '../deletedialog/deletedialog.component';
import { NewDialogComponent } from '../newdialog/newdialog.component';
import { Subscription } from 'src/app/shared/models/subscription.model'
import { AngularFirestore } from '@angular/fire/firestore';
import { Observable } from 'rxjs';



var ELEMENT_DATA: Subscription[] = [];


@Component({
  selector: 'subscriptions-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss'],
})
export class TableComponent implements OnInit {
  public subscription: Subscription
  public status: string
  public message: string
  public error: string
  public mobile: boolean
  public ELEMENT_DATA: Subscription[]
  snackBarDurationInSeconds = 5
  snackBarHorizontalPosition: MatSnackBarHorizontalPosition = 'right'
  snackBarHerticalPosition: MatSnackBarVerticalPosition = 'bottom'

  displayedColumns: string[] = [
    'name',
    'lastname',
    'city',
    'country',
    'whatsapp',
    'email',
    'action'
  ]
  dataSource: MatTableDataSource<Subscription>;
  subscriptions: Observable<any>;
  subscriptionsCollection : Array<Subscription>;
  
  @ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: false}) sort: MatSort;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _subscriptionService: SubscriptionService,
    private _snackBar: MatSnackBar,
    private _httpClient: HttpClient,
    private _token: HttpClient,
    public _dialog: MatDialog,
    private firestore: AngularFirestore
  ) {
    this.message = ''
    this.error = ''
    this.mobile = false;
    this.subscriptions = firestore.collection('subscriptions').get();
    this.subscriptionsCollection = [];
  }

    ngOnInit(){
    console.log(this.subscriptions);

    if (window.screen.width === 360) { 
      this.mobile = true;
    }

    this.subscriptions.subscribe(querySnapshot => {
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        this.subscriptionsCollection.push(doc.data());
        console.log(doc.id, " => ", doc.data());
      });  
      ELEMENT_DATA = this.subscriptionsCollection;
      this.dataSource = new MatTableDataSource(ELEMENT_DATA);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      console.log(ELEMENT_DATA);
      //this.showSnackBar(
        //'Subscripciones cargados correctamente',
        //'success-snackbar',
       // 3
      //)
    });
  }

  openUpdateDialog(element): void {
    let copy = {...element};
    const dialogRef = this._dialog.open(EditdialogComponent, {
      width: '350px',
      data: copy,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(changes => {
      this._subscriptionService.updateArticle(changes).subscribe(
        (response) => {
            this.status = response.status
            console.log('RESPUESTA DE SERVIDOR:')
            console.log(response.data)
            ELEMENT_DATA = ELEMENT_DATA.map(article => {
              return article.id === response.data.id ? response.data : article;
             });
            this.dataSource = new MatTableDataSource(ELEMENT_DATA);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            console.log(ELEMENT_DATA)
            this.showSnackBar(
              'Articulo guardado correctamente',
              'success-snackbar',
              3
            );
        },
        (error) => {
          this.showSnackBar(
            'Error al actualizar el articulo',
            'warning-snackbar',
            3
          )
          console.log(error)
        },
      );  
    });
  }

  openDeleteDialog(element): void {
    let copy = {...element};
    const dialogRef = this._dialog.open(DeleteDialogComponent, {
      width: '350px',
      data: copy,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(article => {
      console.log(article)
      this._subscriptionService.deleteArticle(article).subscribe(
        (response) => {
            this.status = response.status
            console.log('RESPUESTA DE SERVIDOR:')
            console.log(article)           
            console.log(ELEMENT_DATA)  
            ELEMENT_DATA.splice(article, 1);   
            console.log(ELEMENT_DATA); 
            this.dataSource = new MatTableDataSource(ELEMENT_DATA);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.showSnackBar(
              'Articulo borrardo correctamente',
              'success-snackbar',
              3
            );
        },
        (error) => {
          this.showSnackBar(
            'Error al borrar el articulo',
            'warning-snackbar',
            3
          )
          console.log(error)
        },
      );  
    });
  }

  openNewDialog(): void {
    let newArticle = {};
    const dialogRef = this._dialog.open(NewDialogComponent, {
      width: '350px',
      data: newArticle,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(article => {
      console.log(article)
      this._subscriptionService.createArticle(article).subscribe(
        (response) => {
            this.status = response.status
            console.log('RESPUESTA DE SERVIDOR:')
            console.log(article)           
            ELEMENT_DATA.unshift(article); 
            this.dataSource = new MatTableDataSource(ELEMENT_DATA);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.showSnackBar(
              'Articulo creado correctamente',
              'success-snackbar',
              3
            );
        },
        (error) => {
          this.showSnackBar(
            'Error al crear el articulo',
            'warning-snackbar',
            3
          )
          console.log(error)
        },
      );  
    });
  }


  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  showSnackBar(message, snackClass, secondsDuration) {
    this._snackBar.open(message, 'Ok', {
      duration: secondsDuration * 1000,
      panelClass: [snackClass],
      horizontalPosition: this.snackBarHorizontalPosition,
      verticalPosition: this.snackBarHerticalPosition,
    })
  }

  sendWhatsapp(subscription){
    window.open('https://api.whatsapp.com/send?phone='+subscription.countryCode+subscription.whatsapp,'_blank');
  }

  sendMail(subscription){
    window.open('https://mail.google.com/mail/?view=cm&fs=1&to='+subscription.email,'_blank'); 
  }

  
}
