
import { Component, OnInit, Inject, Console} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

import { FormGroup, FormControl, Validators, AbstractControl, ValidatorFn } from '@angular/forms';
import { Subscription } from 'src/app/shared/models/subscription.model';


@Component({
  selector: 'article-newdialog',
  templateUrl: './newdialog.component.html',
  styleUrls: ['./newdialog.component.scss']
})
export class NewDialogComponent  {
  
  updateArticleForm = new FormGroup({
    price: new FormControl('', [
      Validators.required,
    ]),
    description: new FormControl('', [
      Validators.required,
    ]),
    jelpi_amount: new FormControl('', [
      Validators.required,
      Validators.pattern('[[0-9]*\.?[0-9]{0,2}')
    ]),
    company_amount: new FormControl('', [
      Validators.required,
      Validators.pattern('[0-9]*\.?[0-9]{0,2}')
    ])
  });

  constructor(
    public dialogRef: MatDialogRef<NewDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Subscription) {}


  updateArticles(): void {
    this.dialogRef.close()
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  toFloat(strnum){
    return parseFloat(strnum);
  }

}


